.footer {
  background: #1a1a1a;
  color: #fff;
  padding: 4rem 0 2rem;

  .container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 2rem;
  }

  .footer-content {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 3rem;
    margin-bottom: 3rem;
  }

  .footer-section {
    h3 {
      color: #fff;
      margin-bottom: 1.5rem;
      font-size: 1.2rem;
      position: relative;
      padding-bottom: 0.5rem;

      &:after {
        content: '';
        position: absolute;
        left: 0;
        bottom: 0;
        width: 50px;
        height: 2px;
        background: #007bff;
      }
    }

    p {
      color: #999;
      margin-bottom: 1rem;
      display: flex;
      align-items: center;
      gap: 0.5rem;

      i {
        color: #007bff;
      }
    }

    ul {
      list-style: none;
      padding: 0;
      margin: 0;

      li {
        margin-bottom: 0.5rem;

        a {
          color: #999;
          text-decoration: none;
          transition: color 0.3s ease;

          &:hover {
            color: #007bff;
          }
        }
      }
    }

    .social-links {
      display: flex;
      gap: 1rem;

      a {
        color: #999;
        font-size: 1.5rem;
        transition: all 0.3s ease;

        &:hover {
          color: #007bff;
          transform: translateY(-3px);
        }
      }
    }
  }

  .footer-bottom {
    text-align: center;
    padding-top: 2rem;
    border-top: 1px solid rgba(255, 255, 255, 0.1);

    p {
      color: #999;
      margin: 0;
    }
  }
}

@media (max-width: 768px) {
  .footer {
    padding: 3rem 0 1.5rem;

    .footer-content {
      gap: 2rem;
    }
  }
}
